import {getDetail} from '../../apis/index.js'
export default {
	data() {
		return {
			list: undefined
		}
	},
	methods: {
		
	},
	async onLoad(option){
		if(this.list == undefined){
			uni.showLoading({
				title: "加载中......"
			})
		}
		try{
			let res = await getDetail(option.id)
			this.list = res
			uni.hideLoading()
		}catch(e){
			//TODO handle the exception
		}
	}
}
